.dropdown {
  display: grid;
  grid-template-columns: repeat(2, min-content);
  align-items: center;
  justify-content: end;
  height: 50px;
  grid-row: 1;

  @include onTablet {
    grid-column: 2;
  }

  &--for-header {
    opacity: 0;

    @include onTablet {
      opacity: 1;
    }
  }

  &__img {
    cursor: pointer;
    width: 18px;
    height: 15px;
    margin-left: 5px;
    transition: transform 0.3s;
  }

  &__lang {
    margin: 0;
  }

  &__link {
    display: none;
    text-decoration: none;
    transition: opacity 0.3s;
  }

  &__text {
    @extend %raleway;
    @include text-decoration ($fz-4xs, $fw-s, $lh-4, $color-2);
  }

  &:hover &__img {
    transform: rotate(90deg);
  }

  &:hover &__link {
    display: block;
  }

  &:hover &__lang {
    display: none;
  }
}
