.menu {
  box-sizing: border-box;
  display: grid;
  grid-template-columns: min-content min-content;
  column-gap: 30px;
  align-items: center;
  justify-content: end;

  @include onTablet {
    column-gap: 40px;
  }

  &__link {
    display: block;
    width: 25px;
    height: 40px;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    transition: 0.3s;
    grid-column: 2;

    @include onTablet {
      height: 26px;
      grid-column: 1;
    }

    &:hover {
      filter: invert(1);
    }

    &--opener {
      background-image: url(../images/menu.svg);
    }

    &--closer {
      background-image: url(../images/cross.svg);
    }
  }
}
